import React, { useState } from 'react';
import ButtonGroup from './ButtonGroup';
import Button from 'components/button/Button';

const ExamplesButtonGroup = () => {
  const [activeHorizontalButton, setActiveHorizontalButton] = useState(0);
  const [activeVerticalButton, setActiveVerticalButton] = useState(0);
  const [isSwitchOn, setIsSwitchOn] = useState(true);

  const handleHorizontalButtonClick = (index) => {
    setActiveHorizontalButton(index);
    console.log("         ")
  };

  const handleVerticalButtonClick = (index) => {
    setActiveVerticalButton(index);
    console.log("         ")
  };

  const handleToggleSwitch = () => {
    setIsSwitchOn(!isSwitchOn);
    console.log("       ")
  };  

  return (
    <>
      <h2><span>1.   :</span></h2>
      <ButtonGroup>
        <Button onClick={() => handleHorizontalButtonClick(0)} active={activeHorizontalButton === 0}></Button>
        <Button onClick={() => handleHorizontalButtonClick(1)} active={activeHorizontalButton === 1}></Button>
        <Button onClick={() => handleHorizontalButtonClick(2)} active={activeHorizontalButton === 2}></Button>
      </ButtonGroup>

      <h2><span>2.   :</span></h2>
      <ButtonGroup vertical>
        <Button onClick={() => handleVerticalButtonClick(0)} active={activeVerticalButton === 0}></Button>
        <Button onClick={() => handleVerticalButtonClick(1)} active={activeVerticalButton === 1}></Button>
        <Button onClick={() => handleVerticalButtonClick(2)} active={activeVerticalButton === 2}></Button>
      </ButtonGroup>

      <h2><span>3.  -:</span></h2>
      <ButtonGroup>
        <Button onClick={handleToggleSwitch} active={isSwitchOn}></Button>
        <Button onClick={handleToggleSwitch} active={!isSwitchOn}></Button>
      </ButtonGroup>
    </>
  );
};

export default ExamplesButtonGroup;